.content {
	margin: 0 auto;
	padding: 0 10px;
	width: 1280px;
}

.main-wrapper {
	height: 100vh;
	background: url("/images/main.jpg") 50% 50% no-repeat;
	background-size: cover;
}

.header-top {
	padding: 40px 0 30px;
	text-align: center;
	position: relative;
}

.logo {
	display: inline-block;
	margin: 0;
}

.request {
	border-radius: 5px;
	background-color: #5baace;
	padding: 10px 20px;
	border: 0;
	color: #fff;
	position: absolute;
	right: 0;
	top: 70px;
	transition: .3s ease;
	outline: none;
	&:hover {
		opacity: .85;
	}
	&:active {
		opacity: .95;
	}
}

.menu {
	list-style: none;
	margin: 0;
	padding: 0;
	display: flex;
	justify-content: space-between;
	&-item {
		a {
			text-decoration: none;
			color: #fff;
			line-height: 1.2;
			font-size: 14px;
			padding: 5px 0;
			outline: none;
			&:hover {
				text-shadow: #fff 0 0 2px;
			}
		}
	}
}

.section-title {
	font-size: 36px;
	font-weight: 700;
	color: #5baace;
	line-height: 1.2;
	text-align: center;
	margin: 40px 0;
}
.circle-container {
	display: flex;
	justify-content: space-around;
}
.about-item {
	flex: 0 1 15%;
	text-align: center;
	&__text {
		font-size: 17px;
		line-height: 1.4;
	}
}
.circle-item {
	display: inline-block;
	width: 180px;
	height: 180px;
	border-radius: 50%;
	border: 3px solid #ba913d;
	text-align: center;
	&:after {
		display: inline-block;
		content: '';
		height: 100%;
		vertical-align: middle;
	}
	.logo-gold {
		display: inline-block;
		vertical-align: middle;
	}
	.text {
		display: inline-block;
		font-weight: 900;
		font-size: 81px;
		vertical-align: middle;
		color: #ba913d;
		position: relative;
		.sub-text {
			position: absolute;
			bottom: -5px;
			right: 0;
			font-size: 21px;
			font-weight: 400;
		}
	}
}

.genplan-bg {
	height: 100vh;
	background: url("/images/genplan.jpg") 50% 50% no-repeat;
	background-size: cover;
}

.slider-card {
	text-align: center;
	text-decoration: none;
	color: #000;
	&:focus {
		outline: none;
	}
	.card-img {
		display: inline-block;
	}
}
.slick-prev,
.slick-next {
	display: block;
	width: 35px;
	height: 65px;
	position: absolute;
	top: calc(50% - 32px);
	cursor: pointer;
	transition: .3s ease;
	&:active {
		transform: translateX(-10px);
	}
}
.slick-prev {
	left: -35px;
	background: url("/images/arrow-slider.png") 50% 50% no-repeat;
}
.slick-next {
	right: -35px;
	background: url("/images/arrow-slider.png") 50% 50% no-repeat;
	transform: rotate(180deg);
	&:active {
		transform: rotate(180deg) translateX(-10px);
	}
}

.infr-slider {
	height: 700px;
	.slider-item {
		opacity: .5;
		transition: .3s ease;
		height: 700px;
		transform: scale(0.8);
		position: relative;
		.slide-text {
			width: 60%;
			padding: 25px;
			position: absolute;
			bottom: 20%;
			right: 0;
			background-color: rgba(186, 145, 61, .8);
			color: #fff;
			font-size: 20px;
		}
	}
	.slick-current {
		opacity: 1;
		z-index: 1000;
		transform: scale(1.2);
		padding: 60px 0;
	}
	.slider-item-1 {
		background: url("/images/img-1.jpg") 50% 50% no-repeat;
		background-size: cover;
	 }
	.slider-item-2 {
		background: url("/images/img-2.jpg") 50% 50% no-repeat;
		background-size: cover;
	}
	.slider-item-3 {
		background: url("/images/img-3.jpg") 50% 50% no-repeat;
		background-size: cover;
	}
	.slider-item-4 {
		background: url("/images/img-4.jpg") 50% 50% no-repeat;
		background-size: cover;
	}
}

.bc-wrapper {
	height: 100vh;
	background: url("/images/img-4.jpg") 50% 50% no-repeat;
	background-size: cover;
	.content {
		height: 100vh;
		position: relative;
	}
	.bc-text {
		width: 50%;
		padding: 25px;
		position: absolute;
		bottom: 20%;
		right: 0;
		background-color: rgba(186, 145, 61, .8);
		color: #fff;
		font-size: 22px;
	}
}

.garant {
	.content {
		&:after {
			display: block;
			content: '';
			clear: both;
		}
	}
}
.garant-img {
	width: 50%;
	float: left;
	img {
		display: block;
		max-width: 100%;
		margin: 0 auto;
	}
}
.garant-text {
	width: 50%;
	float: left;
	.text-block {
		width: 80%;
		margin: 200px auto 0;
		font-size: 22px;
		font-weight: 500;
	}
}

.building {
	text-align: center;
}
.building-progress {
	display: inline-block;
	position: relative;
	width: 220px;
	height: 220px;
}
.shadow-outer {
	position: absolute;
	top: 5px;
	left: 5px;
	width: 210px;
	height: 210px;
	border-radius: 50%;
	box-shadow: 0 0 15px -6px;
	z-index: -2;
}
.shadow-inner {
	position: absolute;
	top: 31px;
	left: 31px;
	width: 158px;
	height: 158px;
	border-radius: 50%;
	box-shadow: 0 0 15px -5px;
	z-index: -1;
}

.buildings {
	margin: 50px 0;
	display: flex;
	justify-content: space-between;
	.build {
		transition: .3s ease;
		width: 30%;
		opacity: .5;
		&:hover {
			opacity: 1;
		}
	}
}

.main-footer {
	background: #1a1e2a;
	padding: 85px 0;
	.content:after {
		display: block;
		content: '';
		clear: both;
	}
}
.big-logo-wrapper {
	width: 50%;
	float: left;
}
.contacts {
	width: 50%;
	float: left;
	text-align: right;
	color: #fff;
	font-size: 24px;
	p:first-child {
		margin-top: 0;
	}
	p:last-child {
		margin-bottom: 0;
	}
}

.request-form {
	min-width: 500px;
	text-align: center;
	h3 {
		margin-bottom: 30px;
	}
	input {
		display: block;
		width: 100%;
		margin-bottom: 25px;
		border: 1px solid #acacac;
		border-radius: 5px;
		padding: 10px;
		font-size: 16px;
	}
	.request-submit {
		border-radius: 5px;
		background-color: #5baace;
		padding: 10px 20px;
		border: 0;
		color: #fff;
		transition: .3s ease;
		outline: none;
		margin: 5px 0;
		&:hover {
			opacity: .85;
		}
		&:active {
			opacity: .95;
		}
	}
}